@import "../../assets/scss/base/setting";
@import "../../assets/scss/mixin/mixins";
@import "../../assets/scss/mixin/basic";
@import "../../assets/scss/mixin/border";

@include b(footer) {
    position: fixed !important;
    left: 0;
    bottom: 0;
    z-index: 1000;
    width: 100%;
    height: pxTorem(96);
    background: var(--daq-white);
    box-shadow: 0 0 pxTorem(20) rgba(0, 0, 0, .1);
    @include border-top();
}

@include b(nav) {
    display: flex;
    margin: 0;
    padding: 0;
    height: 200%;
    list-style: none;

    @include e(item) {
        flex: 1;
        height: 200%;
        a {
            position: relative;
            display: block;
            font-size: 0;
            text-align: center;
            height: 100%;
            text-decoration: none;
            color: var(--daq-gray-4);
            transtion: all .25s ease 0s;
        }

        .text {
            display: block;
            font-size: pxTorem(22);
        }

        .ued-mobile {
            position: relative;
            padding-top: pxTorem(6);
            display: inline-block;
            width: pxTorem(48);
            height: pxTorem(56);
            line-height: pxTorem(56);
            font-size: pxTorem(34);
        }

        @include when(active) {
            a {
                color: var(--daq-primary-color);
            }

            .ued-mobile {
                animation-name: scale;
                animation-duration: .5s;
                animation-timing-function: ease-in-out;
                animation-iteration-count: 1;
            }
        }
    }

    @keyframes scale {
        0% {
            font-size: pxTorem(40);
        }

        50% {
            font-size: pxTorem(46);
        }

        100% {
            font-size: pxTorem(40);
        }
    }
}